 *
 * Replace them with your own styles or remove the file completely.
 */

.container {
	padding: 80px;
	background-color: white;
	width: 900px;
}

.container__header {
	margin-bottom: 20px;
}

.container__header .container__subtitle {
	color: #676767;
	font-size: 18px;
	font-weight: 500;
	text-transform: uppercase;
	padding: 0;
	margin: 0;
}

.container__header .container__title {
	color: #001a53;
	font-size: 32px;
	font-weight: bold;
	margin-bottom: 15px;
}

.container__header .container__description {
	font-size: 16px;
	color: #676767;
}

.image-row {
	display: flex;
	justify-content: space-between;
	
}

.image-card {
	width: 40%;
	text-align: center;
	padding: 10px;
	position: relative;
	transition: box-shadow 0.3s;
}

.image-card img {
	width: 275px;
	height: auto;
	transition: opacity 0.3s;
	position: relative; /* Needed for absolute positioning */
}

.social-bar {
	position: absolute;
	bottom: 157px;
	width: 275px;
	left: 13px;
	height: 50px;
	background-color: rgba(12, 15, 71, 0.5); /* Transparent blue */
	opacity: 0; /* Invisible by default */
	transition: opacity 0.5s; /* Smooth transition */
}

.image-card img:hover + .social-bar {
	opacity: 1; /* Social bar appears on hover */
}

.social-bar img {
	width: 50%;
	margin: 20px 0;
}

.image-card .card-title {
	color: #001a53;
	font-size: 20px;
	font-weight: bold;
	margin-top: 10px;
}

.image-card .card-subtitle {
	font-size: 12px;
	color: #676767;
	font-weight: 200;
	margin-bottom: 10px;
}

.image-card .card-description {
	color: #676767;
	font-size: 14px;
	color: #666;
}

.container__section {
	margin-bottom: 52px;
}

.section-break {
	margin: 30px;
	display: flex;
	justify-content: center;
}
